import React from 'react'
import mixpanel from 'mixpanel-browser'
import { useNavigate } from 'react-router-dom'

import {
  Box,
  Button,
  Center,
  Flex,
  Image,
  Spacer,
  Text
} from '@chakra-ui/react'

import {
  HeaderFooterWrapper,
  updateIntercomDisplay
} from './ApplicationCommon'

import otter1 from './icons/otter1.svg'
import otter2 from './icons/otter2.svg'
import otter3 from './icons/otter3.svg'

interface TextImagePairProps {
  image: string
  text: string
  subtext: string
  flexDirection: any
}
function TextImagePair (props: TextImagePairProps): React.ReactElement {
  return (
    <Flex
      flexWrap="wrap-reverse"
      flexDirection={props.flexDirection}
      w={['80%', '40%']}
      mt={['12%', '8%']}
    >
      <Box w={['100%', '60%']}>
        <Text fontSize={['1.1em', '1.5em']} fontWeight="500">
          {props.text}
        </Text>
        <Center>
          <Text
            fontSize={['0.7em', '0.9em']}
            mt="5%"
            w={['100%', '80%']}
          >
            {props.subtext}
          </Text>
        </Center>
      </Box>
      <Spacer />
      <Image w={['40%', '35%']} src={props.image} pb={['10%', '0']} />
    </Flex>
  )
}

class TextImagePair3 extends React.Component<TextImagePairProps> {
  constructor (props: TextImagePairProps) {
    super(props)
    this.state = {
      coinbaseResponse: null
    }
  }

  async componentDidMount () {
    const coinbaseResponse = await fetch(
      'https://api.coinbase.com/v2/prices/SOL-USD/spot'
    ).then(resp => resp.json())
    this.setState({ coinbaseResponse: coinbaseResponse })
  }

  render () {
    const OTTER_FEE_SOL = 0.000615 + 0.015 // Deposit + Withdraw
    const otterFeeText = this.state.coinbaseResponse === null
      ? 'less than $0.50'
      : '$' + (this.state.coinbaseResponse.data.amount * OTTER_FEE_SOL).toFixed(2)
    return (
      <TextImagePair
        image={this.props.image}
        text={this.props.text}
        subtext={this.props.subtext.replace('{}', otterFeeText)}
        flexDirection={this.props.flexDirection}
      />
    )
  }
}

export default function Homepage (): React.ReactElement {
  updateIntercomDisplay()
  const navigate = useNavigate()
  return (
    <HeaderFooterWrapper currentPage="homepage">
      <Flex
        flexDirection="column"
        w="100%"
        mt={['5%', '3%']}
      >
        <Text
          fontWeight="600"
          fontSize={['1.3em', '2.1em']}
        >
          A privacy layer for the Solana ecosystem.
        </Text>
        <TextImagePair
          image={otter2}
          text="Stop being followed by trackers, bots, and other creepy things."
          subtext="Otter Cash lets you anonymize your SOL.
            Transfer funds to a new wallet without revealing your identity."
          flexDirection="row"
        />
        <TextImagePair
          image={otter1}
          text="Use Tornado Cash’s ultra-secure smart contract."
          subtext="Otter Cash is Tornado Cash on Solana.
            Tornado is one of the most trusted and audited protocols in the Ethereum ecosystem."
          flexDirection="row-reverse"
        />
        <TextImagePair3
          image={otter3}
          text="Privacy for less than a dollar."
          subtext="Otter Cash costs {} to anonymize 1 SOL.
            Solana’s scale enables Otter be the cheapest anonymity protocol ever."
          flexDirection="row"
        />
        <Button
          variant="fancy"
          w={['70%', '20%']}
          mt={['22%', '10%']}
          padding={['0.9em', '1.3em']}
          fontSize={['0.8em', '1.0em']}
          border={['1px', '2px']}
          onClick={() => {
            mixpanel.track('Go to the App - Button Click')
            navigate('/deposit')
          }}
        >
          Go to the App
        </Button>
      </Flex>
    </HeaderFooterWrapper>
  )
}
